.typewriter {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .13em solid rgb(255, 131, 3); /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .13em; /* Adjust as needed */
  animation: 
    typing 5.5s steps(40, end),
    blink-caret .85s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: rgb(255, 131, 3); }
}
.typewriter-box{
        
            display: inline-block;
}

.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 3000ms ease-out, transform 600ms ease-out,
    visibility 1200ms ease-out;
  will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* Dropup Button */
.dropbtn {
  background-color: #11100d;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
}

/* The container <div> - needed to position the dropup content */
.dropup {
  position: fixed;
  bottom: 20px;
  right: 0;
  z-index: 2;
}

/* Dropup content (Hidden by Default) */
.dropup-content {
  display: none;
  position: absolute;
  bottom: 60px;
  right: 0;
  background-color: rgb(7, 6, 6);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropup */
.dropup-content a {
  color: rgb(255, 254, 193);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-size: x-large;
}

/* Change color of dropup links on hover */
.dropup-content a:hover {
  background-color: rgb(197, 144, 74)}

/* Show the dropup menu on hover */
.dropup:hover .dropup-content {
  display: block;
}

/* Change the background color of the dropup button when the dropup content is shown */
.dropup:hover .dropbtn {
  background-color: #11100d;
}
