#menu-contact{
    width: 150px;
    height: 150px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -75px 0 0 -75px;
    list-style: none;
    font-size: 200%;
    
}

.menu-contact-button {
    opacity: 0;
    z-index: -1;
}

.menu-contact-button {

    width: 150px;
    height: 150px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    background: #635858;
    background-size: 100%;
    overflow: hidden;
    text-decoration: none;
    font-size: xx-large;
    display: inline-block;
    line-height: 40px;
    padding-top: 25%;
    text-align: center;
    color: rgb(255, 131, 3);
    
}

#menu-contact:not(:target)>a:first-of-type,
#menu-contact:target>a:last-of-type {
    opacity: 1;
    z-index: 1;
}

#menu-contact:not(:target)>.icon-plus:before,
#menu-contact:target>.icon-minus:before {
    opacity: 1;
}

.menu-item {
    width: 80px;
    height: 80px;
    position: absolute;
    left: 55%;
    line-height: 5px;
    top: 50%;
    padding-left: 10%;
    padding-bottom: 20%;
    margin: -50px 0 0 -50px;
    border-radius: 50%;
    background-color: #635858;
    transform: translate(0px, 0px);
    transition: transform 500ms;
    z-index: 0;
    transition: .5s;
}

.menu-item:hover{
    opacity: 1;
    box-shadow: 0 2px 30px rgb(143, 141, 141); 
}

.menu-contact-button:hover{
    opacity: 1;
    box-shadow: 0 2px 30px rgb(143, 141, 141); 
}

.menu-item a {
    color: #fff;
    position: relative;
    top: 30%;
    left: 0;
    text-decoration: none;
}
/*twitter*/
#menu-contact:target>.menu-item:nth-child(6) {
    transform: rotate(60deg) translateY(-150px) rotate(300deg);
    transition-delay: 0.17s;
}
/*instagram*/
#menu-contact:target>.menu-item:nth-child(5) {
    transform: rotate(20deg) translateY(-155px) rotate(-20deg);
    transition-delay: 0.2s;
}
/*github*/
#menu-contact:target>.menu-item:nth-child(3) {
    transform: rotate(-20deg) translateY(-155px) rotate(20deg);
    transition-delay: 0.3s;
}
/*linkedin*/

/*nnuevo1*/

#menu-contact:target>.menu-item:nth-child(4) {
    transform: rotate(-60deg) translateY(-145px) rotate(-300deg);
    transition-delay: 0.4s;
}

#menu-contact:target>.menu-item:nth-child(7) {
    transform: rotate(-10deg) translateX(-150px) rotate(10deg);
    transition-delay: 0.5s;
}

#menu-contact:target>.menu-item:nth-child(8) {
    transform: rotate(10deg) translateX(160px) rotate(-10deg);
    transition-delay: 0.1s;
}

